function menuMobile() {
    $('header .button-hamburger').on('click', function() {
        let heightHeader = $('header').outerHeight()
        $('header .mobile-wrap').toggleClass('active').css('top', heightHeader)
    });
    $('header .mobile-wrap .button-close').on('click', function() {
        $('header .mobile-wrap').toggleClass('active')
    });
}

