$(function() {
    const numberFormat = new Intl.NumberFormat();
    $("#slider-range").slider({
        range: true,
        min: 0,
        max: 1000000,
        values: [100000, 700000],
        slide: function(event, ui) {
            $(".min-price").html(numberFormat.format(ui.values[0]) + ' đ ');
            $(".max-price").html(numberFormat.format(ui.values[1]) + ' đ');
            // $(".filter-price").val("$" + ui.values[0] + " - $" + ui.values[1]);
        }
    });
    // $(".min-price").html(numberFormat.format(ui.values[0]));
    // $(".max-price").html(numberFormat.format(ui.values[1]));
    // $("#amount").val("$" + $("#slider-range").slider("values", 0) +
    //     " - $" + $("#slider-range").slider("values", 1));
});

function backToTop() {
    let btn = $('.backtotop')
    btn.on('click', function() {
        $("html, body").animate({
            scrollTop: 0
        }, 1000)
    })
}

function tabs() {
    $("#tabs").tabs();

}

function xoay() {
    $('.side-bar .has-dropdown2 em').on('click', function() {
        $(this).toggleClass('active-xoay').parents('.side-bar').find('.down-content').slideToggle();

    });


    let btn = $('.side-bar .list-cata .has-dropdown em');
    let nav = $('.side-bar .sub-nav');
    btn.on('click', function() {
        // console.log(123);
        nav.slideUp();
        if ($(this).parents('.has-dropdown').hasClass('active')) {
            $(this).parents('.has-dropdown').find('.sub-nav').slideUp();
            $('.side-bar .list-cata .has-dropdown').removeClass('active')
        } else {
            $('.side-bar .list-cata .has-dropdown').removeClass('active')
            $(this).parents('.has-dropdown').addClass('active').find('.sub-nav').slideDown();
        }
    })

    $('.comment-list .reply-wrapper a ').on('click', function() {
        $('.jcmt .cmteditarea').slideToggle();
        $('.jcmt .cmtbtn').slideToggle();
    });


}

function moveHtml() {
    $('.home-banner-kmdb-1').appendTo('.image-wrap-slide-1');
    $('.home-banner-kmdb-2').appendTo('.image-wrap-slide-2')
}

$(document).on('scroll', function() {
    header.scrollActive()
});

$(document).ready(function() {
    setBackgroundElement();

    // header
    mappingMenu();
    menuMobile();
    header.initHeader();

    moveHtml();

    tabs();
    backToTop();
    // swiper
    swiperInit();
    xoay();

});